import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { nanoid } from 'nanoid';
import ContentLoader from 'react-content-loader';
import PopUpCategory from '../popup-category/popup-category';

import { useDispatch, useSelector } from 'react-redux';

export function SidebarCategoryFilter({blockTitle, categories, isLoading, isError}) {
  const dispatch = useDispatch();
  const selectedCategoryIds = useSelector((state) => state.checked.categories);

  let content;
  const [ visibleAllCategory, setVisibleAllCategory ] = useState(false);
  // console.log(categories);
  // loader
  for(let count=0;  count < 6; count++) {
    content = (
      <>
        {content}
        <ContentLoader
          key={nanoid()}
          width={100}
          height={129}
          // viewBox="0 0 400 160"
          backgroundColor="#f3f3f3"
          foregroundColor="#ecebeb"
        >
          <rect x="0" y="0" rx="10" ry="10" width="100" height="100" />
          <rect x="10" y="115" rx="2" ry="2" width="80" height="10" />
        </ContentLoader>
      </>
    );
  }

  if(!isLoading) {
    content = '';

    for (const i in categories) {
      const { title, slug, id, picture_png: picturePng } = categories[i];

      if(selectedCategoryIds.length && selectedCategoryIds.includes(id)) {
        content = (
          <>
            {content}
            <li key={id} className="block-filter__item">
              {/*<input className="block-filter__input visually-hidden" id={slug} type="checkbox" name="category" value={slug} />*/}
              {/* was <label htmlFor={slug}>*/}
              <div className="block-filter__label" htmlFor={slug}>
                <picture>
                  <source media="(max-width: 768px)" srcSet="/img/courses-page/category-placeholder-mobile.jpg" />
                  <img className="block-filter__img" src={picturePng ? picturePng : '/img/courses-page/category-placeholder.jpg'} width="99px" height="99px" alt="" />
                </picture>
              </div>
              <span className="block-filter__name">{title}</span>
            </li>
          </>
        );
      }
    }
  }

  return (
    <>
      <div className="block-filter">
        <h2 className="block-filter__title">{blockTitle}</h2>
        <ul className="block-filter__list">
          {content}
        </ul>
        <button className="block-filter__all btn btn--courses" href="#" onClick={() => setVisibleAllCategory(true)}>Все категории</button>
      </div>
      {!isLoading &&
      <PopUpCategory
        visible={visibleAllCategory}
        setVisible={setVisibleAllCategory}
        categories={categories}
      />}
    </>
  );
}

SidebarCategoryFilter.propTypes = {
  blockTitle: PropTypes.string.isRequired,
  // categories: PropTypes
  //   .shape({
  //     ids: PropTypes.arrayOf(PropTypes.number).isRequired,
  //     // entities: PropTypes.Array.isRequired,
  //     entities: PropTypes.shape({
  //       title: PropTypes.string,
  //       // [PropTypes.string]: title,
  //     }).isRequired,
  //   })
  //   .isRequired,
  categories: PropTypes.array.isRequired,
  isLoading: PropTypes.bool.isRequired,
  isError: PropTypes.bool.isRequired,
};

SidebarCategoryFilter.defaultProps = {
  categories: [],
  isLoading: true,
};
