import React, { useState, useEffect, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import useDocumentTitle from '../../utils/useDocumentTitle';
import PageLayout from '../page-layout/page-layout';
import PropTypes from 'prop-types';

import { PageHeader } from '../page-header/page-header';
import PageHeaderPic from '../../img/page-header/page-header-2.svg';
import SloganImg from '../../img/page-header/slogan-2.svg';

import {SidebarCategoryFilter} from '../sidebar-category-filter/sidebar-category-filter';
import {CoursesList} from '../courses-list/courses-list';

import { useGetCategoriesQuery } from '../../store/api/categories-slice';
import { useGetCoursesQuery } from '../../store/api/courses-slice';

import Message from '../message/message';

const POSTS_PER_PAGE = 6;

export default function PageCourses({title}) {
  useDocumentTitle(title);

  const dispatch = useDispatch();
  const selectedCategoryIds = useSelector((state) => state.checked.categories);
  // перенести в SidebarCategoryFilter?
  const {
    data: categories,
    isFetching: isCategoriesLoading,
    isError: isCategoriesError,
    refetch: refetchCategories,
  } = useGetCategoriesQuery();
  // console.log(categories);

  const [ paged, setPaged ] = useState(1);
  const [ courses, setCourses ] = useState([]);
  const [ selCategories, setSelCategories ] = useState(selectedCategoryIds[0]);

  const {
    data: coursesPaged,
    isFetching: isCoursesLoading,
    isError: isCoursesError,
    refetch: refetchCourses,
  } = useGetCoursesQuery(`?posts_per_page=${POSTS_PER_PAGE}&paged=${paged}&categories=${selCategories}`);
  
  coursesPaged && ! isCoursesLoading &&
  (courses.length < POSTS_PER_PAGE * paged) &&
  setCourses([...coursesPaged, ...courses]); // наборот?

  const refetchAllEndpoints = useCallback(() => {
    refetchCourses();
    refetchCategories();
  }, [
    refetchCourses,
    refetchCategories,
  ]);

  // on change category
  useEffect(() => {
    setPaged(1);
    setCourses([]);
    refetchCourses();
    setSelCategories(selectedCategoryIds[0]);
  }, selectedCategoryIds);

  let isFetchingError = isCoursesError || isCategoriesError;

  let content;

  if(isFetchingError) {
    content = 
      <>
        <PageHeader headerPic={ PageHeaderPic } sloganImg={ SloganImg } visibleButtonAdd={false} />
        <Message onButtonClick={refetchAllEndpoints} />;
      </>

  } else {
    content = (
      <>
        <PageHeader headerPic={ PageHeaderPic } sloganImg={ SloganImg } visibleButtonAdd={false} />
        <div className="list-layout container">
          <div className="list-layout__block-filter" aria-label="Выбор категорий курсов">
            <SidebarCategoryFilter
              blockTitle = 'Категории'
              categories= {categories}
              isLoading = {isCategoriesLoading}
              isError={isCategoriesError}
            />
          </div>

          <div className="list-layout__block-list block-list">
            <form className="block-list__form" action="">
              <input className="block-list__search" type="search" placeholder="Поиск по курсам" />
            </form>

            {/*<div className='block-list__products products products--courses'>*/}
            <CoursesList
              courses={courses}
              isLoading={isCoursesLoading}
              paged={paged}
              setPaged={setPaged}
            />
            {/*</div>*/}
          </div>
        </div>
      </>
    );
  };

  return (
    <PageLayout>
      {content}
    </PageLayout>
  );
}

PageCourses.propTypes = {
  title: PropTypes.string.isRequired,
};
